import { getHttp,putHttp,postHttp, deleteHttp } from "@/utils/http.js";

// 获取用户列表
let getUsers = (query,pagenum ,pagesize )=>{
    return getHttp(`users?query=${query}&pagenum=${pagenum}&pagesize=${pagesize}`)
}

// 修改用户状态
let usersState = ( uId  , type  )=>{
   return putHttp(`users/${uId}/state/${type}`)
}

// 添加用户
let addUsers = (data)=>{
    return  postHttp('users',data)
}
// 获取用户信息
let getUsersMsg = (id)=>{
    return getHttp(`users/${id}`)
}

// 编辑用户 
let updateUser = (id,data)=>{
    return putHttp(`users/${id}`,data)
}

// 删除用户
let removeUser = id => deleteHttp(`users/${id}`);

// 分配角色
let setRoles = (userId,data) => putHttp(`users/${userId}/role`,data);
export {
    getUsers,
    usersState,
    addUsers,
    getUsersMsg,
    updateUser,
    removeUser,
    setRoles
}